





























































































































































import { Vue, Component, Watch, Prop } from "vue-property-decorator";
import utils from "@/utils/utils";
import dictList from "@/utils/dictList";
import SensorTypeAttribute from "./components/SensorTypeAttribute.vue";
import api from "@/microApps/sensorManage/api";

@Component({
  components: {
    SensorTypeAttribute
  }
})
export default class SensorType extends Vue {
  treeData = [];
  // 配置选项
  defaultProps = {
    children: "children",
    label: "name"
  };
  // 选中表单信息
  basicForm: any = {};
  // 弹窗表单信息
  sensorTypeDialog: boolean = false;
  sensorTypeLoading: boolean = false;
  dialogBasicForm: any = {
    positioner: false
  };
  addRules = {
    name: { required: true, message: "请输入名称", trigger: "blur" },
    code: { required: true, message: "请输入编码", trigger: "blur" },
    originalCoordinateType: { required: true, message: "请输入编码", trigger: "blur" },
    centerCoordinateType: { required: true, message: "请输入编码", trigger: "blur" },
    centerCoordinate: { required: true, message: "请输入编码", trigger: "blur" },
    positioner: { required: true, message: "请选择类型", trigger: "change" }
  };
  paging: any = {
    current: 1,
    size: 10,
    total: 0,
    query: {
      name: "",
      code: ""
    }
  };
  coordinateTypeList: any[] = [];
  centerCoordinateTypeList: any[] = [];

  created() {
    this.getSensorTypeTree();
    this.getCenterCoordinateType();
    this.getTargetType();
  }

  // 获取树数据
  getSensorTypeTree() {
    this.$http.get(api.sensorTypeTree, {}).then((res: any) => {
      if (res.data.status) {
        this.treeData = res.data.data;
        !this.basicForm.id && this.treeData.length && this.handleNodeClick(this.treeData[0]);
      } else {
        this.$message.error(res.data.message);
      }
    });
  }

  // 获取基础目标坐标系类型
  async getTargetType() {
    let {
      data: { data, status, message }
    } = await this.$http.post(dictList.requestUrl, {
      dictTypeCode: dictList.dict.targetCoordinateType
    });
    if (status) {
      utils.removeEmptyChildren(data);
      this.coordinateTypeList = data;
    } else {
      this.$message.error("目标坐标系类型查询失败：" + message);
    }
  }

  // 获取中心点可用坐标系类型
  async getCenterCoordinateType() {
    let {
      data: { data, status, message }
    } = await this.$http.post(dictList.requestUrl, {
      dictTypeCode: dictList.dict.coordinateType
    });
    if (status) {
      utils.removeEmptyChildren(data);
      this.centerCoordinateTypeList = data;
    } else {
      this.$message.error("目标坐标系类型查询失败：" + message);
    }
  }

  // 树节点点击事件
  handleNodeClick(data: any) {
    this.basicForm = data;
  }

  // 打开节点弹窗
  openSensorTypeForm(buttonName: any, data: any) {
    this.sensorTypeDialog = true;
    this.$nextTick(() => {
      let refs: any = this.$refs.dialogBasicForm;
      refs.resetFields();
      switch (buttonName) {
        case "add":
          this.dialogBasicForm = {
            id: "",
            parentId: data.id,
            parentName: data.name,
            name: "",
            code: "",
            remark: "",
            positioner: false,
            level: data.level + 1
          };
          break;
        case "edit":
          // utils.mapValue(this.dialogBasicForm, data);
          this.$set(this, "dialogBasicForm", data);
          break;
        case "addTop":
          this.dialogBasicForm = {
            id: "",
            parentId: null,
            parentName: "",
            name: "",
            code: "",
            remark: "",
            positioner: false,
            level: 1
          };
          break;
      }
    });
  }

  // 保存树节点
  saveSensorType() {
    let refs: any = this.$refs.dialogBasicForm;
    refs.validate((valid: boolean) => {
      if (valid) {
        this.sensorTypeLoading = true;
        let url = this.dialogBasicForm.id ? api.updateSensorType : api.addSensorType;
        this.$http
          .post(url, this.dialogBasicForm)
          .then((res: any) => {
            if (res.data.status) {
              this.$message.success("操作成功");
              this.sensorTypeDialog = false;
              this.basicForm = {};
              this.getSensorTypeTree();
            } else {
              this.$message.error(res.data.message);
            }
          })
          .finally(() => {
            this.sensorTypeLoading = false;
          });
      }
    });
  }

  // 删除节点
  remove(node: any, data: any) {
    this.$confirm("删除该类型将会删除其所有子类型及下属数据，是否删除？", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    })
      .then(() => {
        this.$http.get(`${api.deleteSensorType}${data.id}`).then((res: any) => {
          if (res.data.status) {
            this.$message.success("操作成功");
            this.basicForm = {};
            this.getSensorTypeTree();
          } else {
            this.$message.error(res.data.message);
          }
        });
      })
      .catch(() => {});
  }
}
