








































































































import { Vue, Component, Prop, Model, Watch } from "vue-property-decorator";
import api from "@/microApps/sensorManage/api";

@Component({
  components: {}
})
export default class SensorTypeAttribute extends Vue {
  @Prop({ type: [String, Number], default: "" })
  sensorTypeId!: string;

  @Prop({ type: Object })
  paging!: any;

  sensorAttributeTableData: any[] = [];
  loading = false;
  tableLoading = false;
  // 添加属性
  addAttributeDialog = false;
  notBindAttributeTableData: Array<any> = [];
  dialogPaging: any = {
    total: 0,
    current: 1,
    size: 10,
    query: {
      name: "",
      code: ""
    }
  };

  // 自定义序号
  indexCustom: number = 0;
  indexCustom2: number = 0;

  created() {
    this.sensorTypeId && this.getSensorTypeAttributeData();
    this.paging.current = 1;
  }

  // 获取已绑定属性数据
  getSensorTypeAttributeData() {
    this.tableLoading = true;
    this.$http
      .post(`${api.pageBysensorTypeId}${this.sensorTypeId}`, this.paging)
      .then((res: any) => {
        if (res.data.status) {
          this.sensorAttributeTableData = res.data.data.records;
          this.paging.total = res.data.data.total;
          this.indexCustom = (res.data.data.current - 1) * res.data.data.size + 1;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .finally(() => {
        this.tableLoading = false;
      });
  }

  // 获取未绑定属性数据
  getnotBindAttributeData() {
    this.loading = true;
    this.$http
      .post(`${api.pageNotBindBysensorTypeId}${this.sensorTypeId}`, this.dialogPaging)
      .then((res: any) => {
        this.loading = false;
        if (res.data.status) {
          this.notBindAttributeTableData = res.data.data.records;
          this.dialogPaging.total = res.data.data.total;
          this.indexCustom2 = (res.data.data.current - 1) * res.data.data.size + 1;
        } else {
          this.$message.error(res.data.message);
        }
      })
      .catch(() => {
        this.loading = false;
      });
  }

  handleAddAttribute() {
    this.addAttributeDialog = true;
    this.getnotBindAttributeData();
  }

  // 添加功能
  handleAttributeChoose(row: any, index: number) {
    this.loading = true;
    this.$http
      .post(api.addSensorTypeAttribute, {
        sensorTypeId: this.sensorTypeId,
        sensorAttributeId: row.id
      })
      .then((res: any) => {
        if (res.data.status) {
          this.$message.success("添加成功");
          this.getnotBindAttributeData();
          this.getSensorTypeAttributeData();
        } else {
          this.$message.error(res.data.message);
        }
      })
      .finally(() => {
        this.loading = false;
      });
  }

  handleSizeChange(val: number) {
    this.paging.current = val;
    this.getSensorTypeAttributeData();
  }

  // 删除功能
  handleDelTypeAttribute(row: any, index: number) {
    this.$confirm("确认删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(() => {
      this.$http
        .post(api.deleteSensorTypeAttribute, {
          sensorTypeId: this.sensorTypeId,
          sensorAttributeId: row.id
        })
        .then((res: any) => {
          if (res.data.status) {
            this.$message.success("删除成功");
            this.getSensorTypeAttributeData();
          } else {
            this.$message.error(res.data.message);
          }
        });
    });
  }
}
